import React from "react";
import PropTypes from 'prop-types'
import OnlineUtils from "eshop/utils/OnlineUtils";
import TooltipInitializer from "eshop/modules/core/components/ui/TooltipInitializer";

export default class Discount extends React.Component {

    constructor(props){
        super(props)
    }

    componentDidUpdate() {
        this.tooltipInitializer.componentDidMountHandler()
    }

    componentWillUpdate() {
        this.tooltipInitializer.componentWillUnmountHandler()
    }

    componentDidMount() {
        this.tooltipInitializer.componentDidMountHandler()
    }

    componentWillUnmount() {
        this.tooltipInitializer.componentWillUnmountHandler()
    }

    render() {
        this.tooltipInitializer = new TooltipInitializer(this.props.value)
        let value = this.tooltipInitializer.render()

        if(this.props.fixer){
            return fixerWrapper(value, this.props.fixer, this.props.hide)
        }else{
            return simpleWrapper(value, this.props.hide, this.props.showOnMobile, this.props.showAll, this.props.simpleWrapperClassName)
        }
    }
}

Discount.defaultProps = {
    showOnMobile: false,
    showAll: false,
    simpleWrapperClassName: ""
}

function fixerWrapper(value, fixer, hide){
    return (
        <div className="u-small-no-spacing-bottom u-medium-no-spacing-bottom u-spacing-l">
            <div className={"u-animate-height " + fixer + (hide ? " u-hide" : "" )}>
                 <div className="js-height-sensitive-element">
                    <div className="u-medium-table u-medium-spacing-l">
                      {value}
                     </div>
                 </div>
            </div>
        </div>
    )
}

function simpleWrapper(value, hide, showOnMobile, showAll, className){
    var toFullWidth = value.props.dangerouslySetInnerHTML.__html.indexOf('<p') > -1;
    if(hide) {
        return (
           <div className={"u-hide " + className}>
                {value}
           </div>
        )
    } else {
        return (
           <div>
               <div className={((showAll && " ") || (showOnMobile ? "u-hide u-small-block u-padding-m " : "u-small-hide u-block ")) + className}>
                    {value}
               </div>

           </div>
        )
    }

}